.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.person-img {
    margin: 20px;
    border-radius: 40%;
    width: 70px;
    height: 70px;
}

.person {
    margin-top: 20px;
    bottom: 0;
    display: flex;
    flex-direction: row;
}

.activity {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    padding: 30px 20px;
}

.info {
    color: blue;
    display: flex;
    flex-direction: row;

}

.add-break {
    margin: 60px 0px 60px;
}


.time-btn {
    background-color: blue;
    color: white;
    border: none;
    border-radius: 3px;
    margin-right: 5px;
    font-size: medium;
    padding: 5px 10px;
}

.time-btn:hover {
    background-color: purple;
}

.activity-details {
    margin: 60px 0px 60px;
}

.activity-completed {
    margin: 60px 0px 60px;
}

.activity-completed-btn {
    background-color: blue;
    color: white;
    border: none;
    border-radius: 3px;
    margin-right: 5px;
    font-size: medium;
    padding: 10px;
    width: 90%;
}
.exercise img {
    height: 250px;
    width: 100%;
}

.exercise {
    border: solid blue 2px;
    border-radius: 8px;
    padding: 20px;
}

.exercise-btn {
    color: white;
    font-size: 20px;
    background-color: blue;
    width: 100%;
    border: none;
    border-radius: 4px;
}

.exercise-btn:hover {
    background-color: purple;
}
.blogs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 45px;
    gap: 45px;
    margin-left: 50px;
    margin-right: 50px;

}

.header-text {
    color: blue;
    text-align: center;
}

.box {
    border: solid blue 2px;
    border-radius: 5px;
    padding: 20px;
}
.gym-container {
    display: grid;
    grid-template-columns: 3.7fr 1.3fr;
}

.gym-main {
    display: grid;
    grid-template-columns: 5;
}

.header {
    color: blue;
    text-decoration: none;
    font-size: 56px;
    display: flex;
    margin: 80px;
    padding: 20;
    grid-gap: 40px;
    gap: 40px;
    text-align: center;
    align-items: center;
}

.exercise-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 45px;
    gap: 45px;
    margin-left: 50px;
    margin-right: 50px;

}

.activity-container {

    background-color: #ADD8E6;
}



@media only screen and (max-width: 600px) {
    .exercise-container {
        grid-template-columns: repeat(1, 1fr);
    }

    .blogs {
        grid-template-columns: repeat(1, 1fr);
    }

}


@media screen and (min-width: 600px) {
    .exercise-container {
        grid-template-columns: repeat(2, 1fr);
    }

    .blogs {
        grid-template-columns: repeat(2, 1fr);
    }


}

@media screen and (min-width:1100px) {
    .exercise-container {
        grid-template-columns: repeat(3, 1fr);
    }

    .blogs {
        grid-template-columns: repeat(3, 1fr);
    }

}
